<template>
  <div class="d-center column m-10">
    <el-steps :active="active" style="width: 50%" finish-status="success">
      <el-step title="配置通道参数"></el-step>
      <el-step title="配置协议参数"></el-step>
    </el-steps>
    <el-form
      :model="channelInfo"
      :rules="rules"
      ref="channelInfo"
      label-width="120px"
      class="mt-20"
      size="small"
      v-show="active == 0"
    >
      <el-form-item label="传输模式:" prop="transport_mode">
        <el-select v-model="channelInfo.transport_mode">
          <el-option label="tcp" :value="1"></el-option>
          <el-option label="rtu" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="传输协议:" prop="transport_protocol">
        <el-select v-model="channelInfo.transport_protocol">
          <el-option label="modbus TCP" :value="1"></el-option>
          <el-option label="modbus RTU" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="工作模式:" prop="working_mode">
        <el-select v-model="channelInfo.working_mode">
          <el-option label="服务端" :value="1"></el-option>
          <el-option label="客户端" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <template v-if="channelInfo.transport_mode == 2">
        <el-form-item label="串口:" prop="serial_ports">
          <el-select v-model="channelInfo.serial_ports">
            <el-option
              :label="item"
              :value="item"
              v-for="(item, index) in serialPortList"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="波特率:" prop="baud_rate">
          <el-select v-model="channelInfo.baud_rate">
            <el-option
              :label="item"
              :value="item"
              v-for="(item, index) in baudRateList"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="数据位:" prop="data_bit">
          <el-select v-model="channelInfo.data_bit">
            <el-option
              :label="item"
              :value="item"
              v-for="(item, index) in databitList"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="停止位:" prop="stop_bit">
          <el-select v-model="channelInfo.stop_bit">
            <el-option label="1" :value="1"></el-option>
            <el-option label="2" :value="2"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="校验位:" prop="check_bit">
          <el-select v-model="channelInfo.check_bit">
            <el-option label="无校验" :value="0"></el-option>
            <el-option label="奇校验" :value="1"></el-option>
            <el-option label="偶校验" :value="2"></el-option>
          </el-select>
        </el-form-item>
      </template>
      <template v-else-if="channelInfo.working_mode == 1">
        <el-form-item label="端口:" prop="port">
          <el-input-number v-model="channelInfo.port"> </el-input-number>
        </el-form-item>
      </template>
      <template v-else>
        <el-form-item label="IP地址:" prop="address">
          <el-input v-model="channelInfo.address"></el-input>
        </el-form-item>
        <el-form-item label="端口:" prop="port">
          <el-input-number v-model="channelInfo.port"> </el-input-number>
        </el-form-item>
      </template>
      <el-form-item>
        <el-button size="small" @click="submitChannel('channelInfo')"
          >下一步</el-button
        >
      </el-form-item>
    </el-form>
    <el-form
      v-if="active == 1"
      :model="protocolInfo"
      :rules="rules"
      ref="protocolInfo"
      label-width="140px"
      class="mt-20"
      size="small"
    >
      <el-form-item label="从站号:" prop="slaveId">
        <el-input-number
          v-model="protocolInfo.slaveId"
          :min="0"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="轮训时间（毫秒）:" prop="pollingTime">
        <el-input-number
          v-model="protocolInfo.pollingTime"
          :min="1000"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="超时时间（毫秒）:" prop="timeout">
        <el-input-number
          v-model="protocolInfo.timeout"
          :min="10000"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="间隔时间（毫秒）:" prop="interval">
        <el-input-number
          v-model="protocolInfo.interval"
          :min="1000"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="模式:" prop="mode">
        <el-radio-group v-model="protocolInfo.mode">
          <el-radio :label="1">请求模式</el-radio>
          <el-radio :label="2">监听模式</el-radio>
        </el-radio-group>
      </el-form-item>
      <!-- <el-form-item label="功能1类型:" prop="function1Type">
        <el-radio-group v-model="protocolInfo.function1Type">
          <el-radio :label="1">线圈</el-radio>
          <el-radio :label="2">寄存器</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="功能2类型:" prop="function2Type">
        <el-radio-group v-model="protocolInfo.function2Type">
          <el-radio :label="1">离散输入</el-radio>
          <el-radio :label="2">寄存器</el-radio>
        </el-radio-group>
      </el-form-item> -->
      <el-form-item label="采集数量:" prop="collectCount">
        <el-input-number
          v-model="protocolInfo.collectCount"
          :min="1"
        ></el-input-number>
      </el-form-item>
      <el-form-item label="上报模式:" prop="reportMode">
        <el-radio-group v-model="protocolInfo.reportMode">
          <el-radio :label="1">全部采集完成</el-radio>
          <el-radio :label="2">立即</el-radio>
        </el-radio-group>
      </el-form-item>

      <el-form-item>
        <el-button size="small" @click="active = 0">上一步</el-button>
        <el-button size="small" @click="submitProtocol('protocolInfo')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    protocolForm: {
      type: Object,
      default: () => ({}),
    },
    channelForm: {
      type: Object,
      default: () => ({}),
    },
    source_id: {
      type: Number,
    },
  },
  data() {
    let validateIP = (rule, value, callback) => {
      const reg =
        /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (!reg.test(value)) {
        callback(new Error("格式错误 格式:XXX.XXX.XXX.XXX"));
      } else {
        callback();
      }
    };
    let validatePort = (rule, value, callback) => {
      if (!(/^[1-9]\d*$/.test(value) && 1 <= 1 * value && 1 * value <= 65535)) {
        callback(new Error("端口为0-65535"));
      } else {
        callback();
      }
    };
    return {
      channelInfo: {},
      protocolInfo: {},
      rules: {
        source_name: [{ required: true, message: "请输入", trigger: "blur" }],
        "protocol_config.driver_protocol": [
          { required: true, message: "请选择", trigger: "change" },
        ],
        "protocol_config.working_mode": [
          { required: true, message: "请选择", trigger: "change" },
        ],
        "protocol_config.address": [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validateIP, trigger: "change" },
        ],
        "protocol_config.port": [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validatePort, trigger: "change" },
        ],
        "protocol_config.identifer": [
          { required: true, message: "请输入", trigger: "blur" },
        ],
        "protocol_config.operation_type": [
          { required: true, message: "请选择", trigger: "change" },
        ],
        "protocol_config.data_type": [
          { required: true, message: "请选择", trigger: "change" },
        ],
        "protocol_config.base_value": [
          { required: true, message: "请输入", trigger: "blur" },
        ],
        "protocol_config.scale_factor": [
          { required: true, message: "请输入", trigger: "blur" },
        ],
        "protocol_config.decimal_point": [
          { required: true, message: "请选择", trigger: "change" },
        ],
        "protocol_config.decimal_point_num": [
          { required: true, message: "请输入", trigger: "blur" },
        ],
        "protocol_config.interval_time": [
          { required: true, message: "请选择", trigger: "change" },
        ],
        "protocol_config.report_method": [
          { required: true, message: "请选择", trigger: "change" },
        ],
      },
      active: 0,
      deviceList: [],
      serialPortList: [],
      baudRateList: [9600, 14400, 19200, 38400, 57600, 115200],
      databitList: [5, 6, 7, 8],
    };
  },
  created() {
    this.channelInfo = {
      ...this.channelForm,
      baud_rate: this.channelForm.baud_rate || 9600,
      data_bit: this.channelForm.data_bit || 8,
      stop_bit: this.channelForm.stop_bit || 1,
      check_bit: this.channelForm.baud_rate || 0,
    };
    this.protocolInfo = {
      ...this.protocolForm,
      interval: this.protocolForm.interval || 1000,
      pollingTime: this.protocolForm.pollingTime || 1000,
      timeout: this.protocolForm.timeout || 1000,
    };
  },
  watch: {
    "channelInfo.transport_mode"(newVal) {
      if (newVal == 2) {
        this.getSerialPortList();
      }
    },
  },
  methods: {
    getSerialPortList() {
      this.serialPortList = [];
      this.$api.get("/collect/getSerialPortList").then((res) => {
        this.serialPortList = res.data.data;
      });
    },
    submitChannel(formName) {
      this.active = 1;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/collect/setChannelConfig", {
              source_id: this.source_id,
              channel_config: this.channelInfo,
            })
            .then(() => {
              this.active = 1;
            });
        } else {
          return false;
        }
      });
    },
    submitProtocol(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/collect/setProtocolConfig", {
              source_id: this.source_id,
              protocol_config: this.protocolInfo,
            })
            .then(() => {
              this.$parent.goBack();
              this.$message.success("操作成功");
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
