<template>
  <div class="d-center column m-10">
    <el-steps :active="active" style="width: 50%" finish-status="success">
      <el-step title="配置通道参数"></el-step>
      <el-step title="配置协议参数"></el-step>
    </el-steps>
    <el-form
      :model="channelInfo"
      ref="channelInfo"
      label-width="140px"
      class="mt-20"
      size="small"
      v-show="active == 0"
    >
      <el-form-item label="接口类型:" prop="interface_type">
        <el-select
          v-model="channelInfo.interface_type"
          @change="getInterfaceList"
        >
          <el-option label="串口" :value="1"></el-option>
          <el-option label="GPIO接口" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="接口列表:" prop="interface_name">
        <el-select v-model="channelInfo.interface_name">
          <el-option
            :label="item"
            :value="item"
            v-for="(item, index) in interfaceList"
            :key="index"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item
        label="波特率:"
        prop="baudrate"
        v-if="channelInfo.interface_type == 1"
      >
        <el-select v-model="channelInfo.baudrate">
          <el-option
            :label="item"
            :value="item"
            v-for="(item, index) in baudrateList"
            :key="index"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="submitChannel('channelInfo')"
          >下一步</el-button
        >
      </el-form-item>
    </el-form>
    <el-form
      v-if="active == 1"
      :model="protocolInfo"
      :rules="rules"
      ref="protocolInfo"
      label-width="140px"
      class="mt-20"
      size="small"
    >
      <el-form-item label="采集间隔时间(毫秒):" prop="interval_time">
        <el-input-number v-model="protocolInfo.interval_time" />
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="active = 0">上一步</el-button>
        <el-button size="small" @click="submitProtocol('protocolInfo')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    protocolForm: {
      type: Object,
      default: () => ({}),
    },
    channelForm: {
      type: Object,
      default: () => ({}),
    },
    source_id: {
      type: Number,
    },
  },
  data() {
    return {
      channelInfo: {},
      protocolInfo: {},
      rules: {},
      active: 0,
      deviceList: [],
      interfaceList: [],
      baudrateList: [2400, 4800, 9600, 19200, 38400, 57600, 115200],
    };
  },
  created() {
    this.channelInfo = {
      ...this.channelForm,
      interface_type: this.channelForm.interface_type || null,
      interface_name: this.channelForm.interface_name || null,
      ...(this.channelForm.interface_type == 1 && {
        baudrate: this.channelInfo.baudrate || 9600,
      }),
    };
    this.protocolInfo = {
      ...this.protocolForm,
      interval_time: this.protocolForm.interval_time || 1000,
    };
    this.getInterfaceList();
  },
  methods: {
    getInterfaceList(e) {
      if (e) {
        this.channelInfo.interface_name = "";
        this.channelInfo.baudrate = e == 1 ? 9600 : undefined;
      }
      let url = e == 2 ? "/collect/getGpioList" : "/collect/getSerialPortList";
      this.$api.get(url).then((res) => {
        this.interfaceList = res.data.data;
      });
    },
    submitChannel(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/collect/setChannelConfig", {
              source_id: this.source_id,
              channel_config: this.channelInfo,
            })
            .then(() => {
              this.active = 1;
            });
        } else {
          return false;
        }
      });
    },
    submitProtocol(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/collect/setProtocolConfig", {
              source_id: this.source_id,
              protocol_config: this.protocolInfo,
            })
            .then(() => {
              this.$parent.goBack();
              this.$message.success("操作成功");
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
