<template>
  <div>
    <el-table
      size="mini"
      :data="attributeList"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column prop="identifier" label="标识符" align="center" />
      <el-table-column prop="describe" label="描述" align="center" />
      <el-table-column prop="data_type" label="数据类型" align="center">
        <template slot-scope="scope">
          {{ data_type.get(scope.row.protocol_config.data_type) }}
        </template>
      </el-table-column>
      <el-table-column
        prop="operation_type"
        label="寄存器操作类型"
        align="center"
      >
        <template slot-scope="scope">
          {{ operation_type.get(scope.row.protocol_config.operation_type) }}
        </template>
      </el-table-column>
      <el-table-column prop="protocol_config" label="寄存器地址" align="center">
        <template slot-scope="scope">
          {{ scope.row.protocol_config.register_address }}
        </template></el-table-column
      >
      <el-table-column
        prop="protocol_config"
        label="是否交换寄存器高低字节"
        align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.protocol_config.swap_high_low == 1 ? "是" : "否" }}
        </template>
      </el-table-column>

      <el-table-column prop="base_value" label="基础值" align="center">
        <template slot-scope="scope">
          {{ scope.row.base_value }}
        </template>
      </el-table-column>
      <el-table-column prop="scale_factor" label="比例因子" align="center">
        <template slot-scope="scope">
          {{ scope.row.protocol_config.scale_factor }}
        </template>
      </el-table-column>
      <el-table-column prop="latest_value" label="最新值" align="center">
        <template slot-scope="scope">
          {{ scope.row.latest_value }}
        </template>
      </el-table-column>
      <!-- <el-table-column prop="interval_time" label="间隔时间" align="center">
        <template slot-scope="scope">
          {{ scope.row.protocol_config.interval_time }}
        </template>
      </el-table-column>
      <el-table-column prop="report_method" label="上报方式" align="center">
        <template slot-scope="scope">
          {{ report_method.get(scope.row.protocol_config.report_method) }}
        </template>
      </el-table-column> -->

      <el-table-column
        prop="interval_time"
        label="上报时间"
        align="center"
        width="200"
      >
        <template slot-scope="scope">
          {{ scope.row.time }}
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="write(scope.row)"
            ><i title="写入" class="icon iconf-write"></i
          ></el-button>
          <el-button
            type="text"
            icon="el-icon-edit"
            @click="editAttribute(scope.row)"
          />
          <el-button type="text" @click="delAttribute(scope.row)">
            <i title="删除" class="el-icon-delete" style="color: red"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      class="float-right mt-10"
      v-show="attributeTotal > 0"
      :total="attributeTotal"
      :page.sync="attributeParams.page"
      :limit.sync="attributeParams.size"
      @pagination="getAttributeList"
      layout="total, prev, pager, next"
      small
    />
    <!-- 属性 -->
    <el-dialog
      :title="attributeVisibleTitle"
      :visible.sync="attributeVisible"
      width="40%"
      :close-on-click-modal="false"
      top="0"
    >
      <el-form
        :model="attributeForm"
        :rules="rules"
        ref="attributeForm"
        label-width="200px"
        size="small"
        style="width: 80%"
      >
        <el-form-item label="标识符:" prop="identifier">
          <el-input v-model="attributeForm.identifier"></el-input>
        </el-form-item>
        <el-form-item label="描述:" prop="describe">
          <el-input v-model="attributeForm.describe"></el-input>
        </el-form-item>
        <el-form-item label="数据类型:" prop="protocol_config.data_type">
          <el-select v-model="attributeForm.protocol_config.data_type">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in data_type.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="寄存器操作类型:"
          prop="protocol_config.operation_type"
        >
          <el-select v-model="attributeForm.protocol_config.operation_type">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in operation_type.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="寄存器地址:"
          prop="protocol_config.register_address"
        >
          <el-input-number
            v-model="attributeForm.protocol_config.register_address"
          ></el-input-number>
        </el-form-item>
        <el-form-item
          label="交换寄存器内高低字节:"
          prop="protocol_config.region"
        >
          <el-checkbox
            v-model="attributeForm.protocol_config.swap_high_low"
            :true-label="1"
            :false-label="2"
          ></el-checkbox>
        </el-form-item>
        <el-form-item label="基值:" prop="protocol_config.base_value">
          <el-input
            v-model="attributeForm.protocol_config.base_value"
          ></el-input>
        </el-form-item>
        <el-form-item label="缩放因子:" prop="protocol_config.scale_factor">
          <el-input-number
            v-model="attributeForm.protocol_config.scale_factor"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="小数位数:" prop="protocol_config.decimal_point">
          <el-select v-model="attributeForm.protocol_config.decimal_point">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in decimal_point.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="小数点个数:"
          prop="protocol_config.decimal_point_num"
          v-if="attributeForm.protocol_config.decimal_point == 2"
        >
          <el-input-number
            v-model="attributeForm.protocol_config.decimal_point_num"
          ></el-input-number>
        </el-form-item>
        <el-form-item
          label="采集间隔时间(毫秒):"
          prop="protocol_config.interval_time"
        >
          <el-input-number
            v-model="attributeForm.protocol_config.interval_time"
          >
          </el-input-number>
        </el-form-item>
        <el-form-item
          label="数据上报方式:"
          prop="protocol_config.report_method"
        >
          <el-select v-model="attributeForm.protocol_config.report_method">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in report_method.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="attributeVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitAttributeForm('attributeForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
    <el-dialog
      title="写入"
      width="40%"
      :visible.sync="writeVisible"
      v-if="writeVisible"
      top="20px"
      :close-on-click-modal="false"
    >
      <el-form
        :model="writeForm"
        :rules="writeFormRules"
        ref="writeForm"
        label-width="0"
        size="small"
      >
        <el-form-item label="" prop="value">
          <div class="d-flex">
            <el-input v-model="writeForm.value" @input="handleInput"></el-input
            ><el-input
              placeholder="请输入内容"
              v-model="writeForm.decimalNumber"
              class="ml-10"
              @input="handleInput16"
            >
              <template slot="prepend">0x</template>
            </el-input>
          </div>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="writeVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitWriteForm()">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    chooseItem: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    const validate10 = (rule, value, callback) => {
      const reg = /^[-+]?\d*\.?\d+$/;
      if (!/^-?\d*.?\d+$/.test(value)) {
        callback(new Error("请输入有效的数字（支持小数"));
      } else if (!reg.test(value)) {
        callback(new Error("请输入有效的十进制"));
      } else {
        callback();
      }
    };
    const validate16 = (rule, value, callback) => {
      const reg = /^[0-9A-Fa-f]+$/;
      if (!reg.test(value)) {
        callback(new Error("请输入有效的十六进制"));
      } else {
        callback();
      }
    };
    return {
      loading: false,
      attributeList: [],
      attributeParams: {
        page: 1,
        size: 10,
      },
      timer: null,
      attributeTotal: 0,
      operation_type: new Map([
        // [1, "读取线圈寄存器"],
        // [2, "读取离散输入寄存器"],
        // [3, "读取保持寄存器"],
        // [4, "读取输入寄存器"],
        // [5, "写单个线圈寄存器"],
        // [6, "写单个保持寄存器"],
        [1, "读取线圈状态"],
        [2, "读取离散输入"],
        [3, "读取保持寄存器"],
        [4, "读取输入寄存器"],
        [5, "写单个线圈"],
        [6, "写多个线圈"],
        [7, "写单个保持寄存器"],
        [8, "写多个保持寄存器"],
      ]),
      data_type: new Map([
        [1, "int8"],
        [2, "int16"],
        [3, "int32"],
        [4, "float"],
        [5, "double"],
        [6, "bool"],
        [7, "string"],
        [8, "bits"],
      ]),
      decimal_point: new Map([
        [1, "默认个数"],
        [2, "指定个数"],
      ]),
      report_method: new Map([
        [1, "按时上报"],
        [2, "变更上报"],
      ]),
      attributeVisible: false,
      attributeForm: {
        protocol_config: {},
      },
      attributeVisibleTitle: "",
      rules: {},
      writeVisible: false,
      writeForm: {},
      decimalNumber: "",
      writeFormRules: {
        value: [
          { required: true, message: "请输入", trigger: ["change", "blur"] },
          { validator: validate10, trigger: ["change", "blur"] },
        ],
        decimalNumber: [
          { required: true, message: "请输入", trigger: ["change", "blur"] },
          { validator: validate16, trigger: ["change", "blur"] },
        ],
      },
    };
  },
  created() {
    this.chooseItemInfo = {
      ...this.chooseItem,
    };
    this.getAttributeList();
  },
  methods: {
    delAttribute(item) {
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$api
            .post("/collect/delDataProperty", {
              pro_id: item.pro_id,
            })
            .then(() => {
              this.$message.success("操作成功");
              this.getAttributeList();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    handleInput16() {
      if (!this.writeForm.decimalNumber) {
        this.writeForm.value = "";
        return;
      }

      // 过滤非法字符（保留0-9A-Fa-f和点号）
      let cleanHex = this.writeForm.decimalNumber.replace(/[^0-9A-Fa-f.]/g, "");

      // 处理0x前缀
      cleanHex = cleanHex.startsWith("0x") ? cleanHex.substring(2) : cleanHex;

      // 分离整数和小数部分
      if (cleanHex.includes(".")) {
        const [intPart, fracPart] = cleanHex.split(".");
        const intValue = parseInt(intPart || "0", 16);
        const fracValue = this.hexFractionToDecimal(fracPart);
        this.writeForm.value = (intValue + fracValue).toString();
      } else {
        const num = parseInt(cleanHex, 16) || 0;
        this.writeForm.value = num.toString();
      }
    },

    handleInput() {
      if (!this.writeForm.value) {
        this.writeForm.decimalNumber = "";
        return;
      }

      // 过滤非法字符（保留数字和点号）
      const cleanValue = this.writeForm.value.replace(/[^0-9.]/g, "");

      // 处理小数转换
      if (cleanValue.includes(".")) {
        const [intPart, fracPart] = cleanValue.split(".");
        const intHex = parseInt(intPart || "0")
          .toString(16)
          .toUpperCase();
        const fracHex = this.decimalFractionToHex("0." + fracPart);
        this.writeForm.decimalNumber = `${intHex}.${fracHex}`;
      } else {
        const num = parseInt(cleanValue) || 0;
        this.writeForm.decimalNumber = num.toString(16).toUpperCase();
      }
    },

    // 十六进制小数部分转十进制
    hexFractionToDecimal(hexFraction) {
      return hexFraction.split("").reduce((acc, char, idx) => {
        return acc + parseInt(char, 16) / Math.pow(16, idx + 1);
      }, 0);
    },

    // 十进制小数部分转十六进制
    decimalFractionToHex(fraction) {
      let result = "";
      let num = parseFloat(fraction);
      for (let i = 0; i < 8 && num > 0; i++) {
        // 限制8位精度防止无限循环
        num *= 16;
        const digit = Math.floor(num);
        result += digit.toString(16).toUpperCase();
        num -= digit;
      }
      return result || "0";
    },
    submitWriteForm() {
      this.$api
        .post("/collect/controlDataProperty", {
          identifier: this.attributeForm.identifier,
          pro_id: this.attributeForm.pro_id,
          value: this.writeForm.value,
        })
        .then(() => {
          this.writeVisible = false;
          this.$message.success("操作成功");
        });
    },
    write(row) {
      this.writeVisible = true;
      this.attributeForm = row;
      this.writeForm = {};
    },
    editAttribute(row) {
      this.attributeVisibleTitle = "编辑属性";
      this.attributeForm = row;
      this.attributeForm.protocol_config = !this.analyzeProperty(
        row.protocol_config
      )
        ? {}
        : {
            ...row.protocol_config,
            decimal_point: row.protocol_config.decimal_point || 1,
          };
      this.attributeVisible = true;
    },
    submitAttributeForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.attributeForm.pro_id) {
            this.$api
              .post("/collect/updateDataProperty", this.attributeForm)
              .then(() => {
                this.attributeVisible = false;
                this.$message.success("操作成功");
                this.getAttributeList();
              });
          } else {
            this.$api
              .post("/collect/addDataProperty", this.attributeForm)
              .then(() => {
                this.attributeVisible = false;
                this.$message.success("操作成功");
                this.getAttributeList();
              });
          }
        } else {
          return false;
        }
      });
    },
    addAttribute() {
      this.attributeVisibleTitle = "添加属性";
      this.attributeForm = {
        source_id: this.chooseItemInfo.source_id,
        protocol_config: {
          swap_high_low: 0,
          base_value: 0,
          scale_factor: 1,
          interval_time: 1000,
          report_method: 2,
          decimal_point: 1,
        },
      };
      this.attributeVisible = true;
    },
    getDataValueList() {
      this.getData();
    },
    getAttributeList() {
      this.attributeList = [];
      this.loading = true;
      this.$api
        .get(`/collect/getDataPropertyList`, {
          params: {
            ...this.attributeParams,
            source_id: this.chooseItemInfo.source_id,
          },
        })
        .then(async (res) => {
          if (res.data.data) {
            this.attributeList = res.data.data.map((item) => ({
              ...item,
              protocol_config: item.protocol_config
                ? JSON.parse(item.protocol_config)
                : {},
            }));

            this.attributeTotal = res.data.total;
            this.getDataValueList();
          } else {
            this.attributeList = [];
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getData() {
      this.$api
        .get("/collect/getDataValueList", {
          params: {
            ...this.attributeParams,
            source_id: this.chooseItemInfo.source_id,
          },
        })
        .then((res) => {
          if (res.data.data) {
            this.attributeList.forEach((item1, index) => {
              const matchedItem = res.data.data.find(
                (item) => item1.pro_id === item.pro_id
              );
              if (matchedItem) {
                // 使用数组索引确保触发响应式
                this.$set(this.attributeList, index, {
                  ...item1,
                  latest_value: matchedItem.value,
                  time: matchedItem.time,
                });
              }
            });
          }
        });
    },
    analyzeProperty(value) {
      if (
        value === null ||
        value === "null" ||
        value === "" ||
        value === undefined
      )
        return false;

      if (typeof value === "object" && value !== null) {
        if (Object.keys(value).length === 0) return false;
      }
      return true;
    },
  },
};
</script>

<style lang="scss" scoped></style>
