<template>
  <div class="d-center column m-10">
    <el-steps :active="active" style="width: 50%" finish-status="success">
      <el-step title="配置通道参数"></el-step>
      <el-step title="配置协议参数"></el-step>
    </el-steps>
    <el-form
      :model="channelInfo"
      :rules="rules"
      ref="channelInfo"
      label-width="140px"
      class="mt-20"
      size="small"
      v-show="active == 0"
    >
      <el-form-item label="传输协议:" prop="transfer_mode">
        <el-select v-model="channelInfo.transfer_mode">
          <el-option label="UDP" :value="1"></el-option>
          <el-option label="TCP" :value="2"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="本地ID:" prop="local_id">
        <el-input v-model="channelInfo.local_id"></el-input>
      </el-form-item>
      <el-form-item label="本地域:" prop="local_domain">
        <el-input v-model="channelInfo.local_domain"></el-input>
      </el-form-item>
      <el-form-item label="本地IP:" prop="local_ip">
        <el-input v-model="channelInfo.local_ip"></el-input>
      </el-form-item>
      <el-form-item label="本地端口:" prop="local_port">
        <el-input-number v-model="channelInfo.local_port"> </el-input-number>
      </el-form-item>
      <el-form-item label="本地密码:" prop="local_pwd">
        <el-input v-model="channelInfo.local_pwd" type="password" show-password>
        </el-input>
      </el-form-item>
      <el-form-item label="本地收流最小端口:" prop="rtp_min_port">
        <el-input-number v-model="channelInfo.rtp_min_port" :min="30000">
        </el-input-number>
      </el-form-item>
      <el-form-item label="本地收流最大端口:" prop="rtp_max_port">
        <el-input-number v-model="channelInfo.rtp_max_port" :min="30000">
        </el-input-number>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="submitChannel('channelInfo')"
          >下一步</el-button
        >
      </el-form-item>
    </el-form>
    <el-form
      v-if="active == 1"
      :model="protocolInfo"
      :rules="rules"
      ref="protocolInfo"
      label-width="140px"
      class="mt-20"
      size="small"
    >
      <el-form-item label="上级平台ID:" prop="parent_id">
        <el-input v-model="protocolInfo.parent_id"></el-input>
      </el-form-item>
      <el-form-item label="上级平台域:" prop="parent_domain">
        <el-input v-model="protocolInfo.parent_domain"></el-input>
      </el-form-item>
      <el-form-item label="上级平台IP:" prop="parent_ip">
        <el-input v-model="protocolInfo.parent_ip"></el-input>
      </el-form-item>
      <el-form-item label="上级平台端口:" prop="parent_port">
        <el-input-number v-model="protocolInfo.parent_port"> </el-input-number>
      </el-form-item>
      <el-form-item label="上级平台sip密码:" prop="parent_pwd">
        <el-input
          v-model="protocolInfo.parent_pwd"
          type="password"
          show-password
        >
        </el-input>
      </el-form-item>
      <el-form-item label="注册间隔(秒):" prop="register_interval">
        <el-input-number v-model="protocolInfo.register_interval">
        </el-input-number>
      </el-form-item>
      <el-form-item label="心跳间隔(秒):" prop="keepalive_interval">
        <el-input-number v-model="protocolInfo.keepalive_interval">
        </el-input-number>
      </el-form-item>
      <el-form-item>
        <el-button size="small" @click="active = 0">上一步</el-button>
        <el-button size="small" @click="submitProtocol('protocolInfo')"
          >提交</el-button
        >
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  props: {
    protocolForm: {
      type: Object,
      default: () => ({}),
    },
    channelForm: {
      type: Object,
      default: () => ({}),
    },
    source_id: {
      type: Number,
    },
  },
  data() {
    let validateIP = (rule, value, callback) => {
      const reg =
        /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/;
      if (!reg.test(value)) {
        callback(new Error("格式错误 格式:XXX.XXX.XXX.XXX"));
      } else {
        callback();
      }
    };
    let validatePort = (rule, value, callback) => {
      if (!(/^[1-9]\d*$/.test(value) && 1 <= 1 * value && 1 * value <= 65535)) {
        callback(new Error("端口为30000-65535"));
      } else {
        callback();
      }
    };
    return {
      channelInfo: {},
      protocolInfo: {},
      rules: {
        source_name: [{ required: true, message: "请输入", trigger: "blur" }],
        local_ip: [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validateIP, trigger: "change" },
        ],
        local_port: [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validatePort, trigger: "change" },
        ],
        parent_ip: [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validateIP, trigger: "change" },
        ],
        parent_port: [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validatePort, trigger: "change" },
        ],
        rtp_min_port: [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validatePort, trigger: "change" },
        ],
        rtp_max_port: [
          { required: true, message: "请输入", trigger: "change" },
          { validator: validatePort, trigger: "change" },
        ],
        local_pwd: [{ required: true, message: "请输入", trigger: "blur" }],
      },
      active: 0,
      deviceList: [],
    };
  },
  created() {
    this.channelInfo = {
      ...this.channelForm,
      local_port: this.channelForm.local_port || 5060,
      rtp_min_port: this.channelForm.rtp_min_port || 30000,
      rtp_max_port: this.channelForm.rtp_max_port || 30500,
      transfer_mode: this.channelForm.transfer_mode || 1,
    };
    this.protocolInfo = {
      ...this.protocolForm,
      register_interval: this.protocolForm.register_interval || 3600,
      keepalive_interval: this.protocolForm.keepalive_interval || 60,
      parent_port: this.protocolForm.parent_port || 5060,
    };
  },
  methods: {
    submitChannel(formName) {
      // this.active = 1;
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/collect/setChannelConfig", {
              source_id: this.source_id,
              channel_config: this.channelInfo,
            })
            .then(() => {
              this.active = 1;
            });
        } else {
          return false;
        }
      });
    },
    submitProtocol(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.$api
            .post("/collect/setProtocolConfig", {
              source_id: this.source_id,
              protocol_config: this.protocolInfo,
            })
            .then(() => {
              this.$parent.goBack();
              this.$message.success("操作成功");
            });
        } else {
          return false;
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped></style>
