<template>
  <div>
    <el-table
      size="mini"
      :data="attributeList"
      style="width: 100%"
      v-loading="loading"
    >
      <el-table-column prop="identifier" label="标识符" align="center" />
      <el-table-column prop="describe" label="描述" align="center" />
      <el-table-column prop="data_type" label="数据类型" align="center">
        <template slot-scope="scope">
          {{ data_type.get(scope.row.protocol_config.data_type) }}
        </template>
      </el-table-column>

      <el-table-column
        prop="protocol_config"
        label="是否交换高低字节"
        align="center"
      >
        <template slot-scope="scope">
          {{ scope.row.protocol_config.swap_high_low == 1 ? "是" : "否" }}
        </template>
      </el-table-column>
      <el-table-column prop="base_value" label="最新值" align="center">
        <template slot-scope="scope">
          {{ scope.row.base_value }}
        </template>
      </el-table-column>
      <el-table-column prop="scale_factor" label="缩放因子" align="center">
        <template slot-scope="scope">
          {{ scope.row.protocol_config.scale_factor }}
        </template>
      </el-table-column>
      <el-table-column prop="report_method" label="上报方式" align="center">
        <template slot-scope="scope">
          {{ report_method.get(scope.row.protocol_config.report_method) }}
        </template>
      </el-table-column>
      <el-table-column
        prop="interval_time"
        label="上报时间"
        align="center"
        width="150"
      >
        <template slot-scope="scope">
          {{ scope.row.time }}
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            type="text"
            icon="el-icon-edit"
            @click="editAttribute(scope.row)"
          />
          <el-button type="text" @click="delAttribute(scope.row)">
            <i title="删除" class="el-icon-delete" style="color: red"></i>
          </el-button>
        </template>
      </el-table-column>
    </el-table>
    <pagination
      class="float-right mt-10"
      v-show="attributeTotal > 0"
      :total="attributeTotal"
      :page.sync="attributeParams.page"
      :limit.sync="attributeParams.size"
      @pagination="getAttributeList"
      layout="total, prev, pager, next"
      small
    />
    <!-- 属性 -->
    <el-dialog
      :title="attributeVisibleTitle"
      :visible.sync="attributeVisible"
      width="40%"
      :close-on-click-modal="false"
      top="5vh"
    >
      <el-form
        :model="attributeForm"
        :rules="rules"
        ref="attributeForm"
        label-width="200px"
        size="small"
        style="width: 80%"
      >
        <el-form-item label="标识符:" prop="identifier">
          <el-input v-model="attributeForm.identifier"></el-input>
        </el-form-item>
        <el-form-item label="描述:" prop="describe">
          <el-input v-model="attributeForm.describe"></el-input>
        </el-form-item>
        <!-- <el-form-item label="操作类型:" prop="protocol_config.operation_type">
          <el-select v-model="attributeForm.protocol_config.operation_type">
            <el-option label="开关量" :value="1"></el-option>
            <el-option label="模拟量" :value="2"></el-option>
          </el-select>
        </el-form-item> -->
        <el-form-item label="起始地址:" prop="protocol_config.start_address">
          <el-input-number
            v-model="attributeForm.protocol_config.start_address"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="数据类型:" prop="protocol_config.data_type">
          <el-select v-model="attributeForm.protocol_config.data_type">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in data_type.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="交换内高低字节:" prop="protocol_config.region">
          <el-checkbox
            v-model="attributeForm.protocol_config.swap_high_low"
            :true-label="1"
            :false-label="2"
          ></el-checkbox>
        </el-form-item>
        <el-form-item label="基值:" prop="protocol_config.base_value">
          <el-input
            v-model="attributeForm.protocol_config.base_value"
          ></el-input>
        </el-form-item>
        <el-form-item label="缩放因子:" prop="protocol_config.scale_factor">
          <el-input-number
            v-model="attributeForm.protocol_config.scale_factor"
          ></el-input-number>
        </el-form-item>
        <el-form-item label="小数位数:" prop="protocol_config.decimal_point">
          <el-select v-model="attributeForm.protocol_config.decimal_point">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in decimal_point.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item
          label="小数点个数:"
          prop="protocol_config.decimal_point_num"
          v-if="attributeForm.protocol_config.decimal_point == 2"
        >
          <el-input-number
            v-model="attributeForm.protocol_config.decimal_point_num"
          ></el-input-number>
        </el-form-item>
        <el-form-item
          label="数据上报方式:"
          prop="protocol_config.report_method"
        >
          <el-select v-model="attributeForm.protocol_config.report_method">
            <el-option
              :label="label"
              :value="value"
              v-for="[value, label] in report_method.entries()"
              :key="value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="attributeVisible = false">取 消</el-button>
        <el-button type="primary" @click="submitAttributeForm('attributeForm')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: {
    chooseItem: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      loading: false,
      attributeList: [],
      attributeParams: {
        page: 1,
        size: 10,
      },
      timer: null,
      attributeTotal: 0,
      data_type: new Map([
        [1, "int8"],
        [2, "int16"],
        [3, "int32"],
        [4, "float"],
        [5, "double"],
        [6, "bool"],
        [7, "string"],
        [8, "bits"],
      ]),
      decimal_point: new Map([
        [1, "默认个数"],
        [2, "指定个数"],
      ]),
      report_method: new Map([
        [1, "按时上报"],
        [2, "变更上报"],
      ]),
      attributeVisible: false,
      attributeForm: {
        protocol_config: {},
      },
      attributeVisibleTitle: "",
      rules: {},
    };
  },
  created() {
    this.chooseItemInfo = {
      ...this.chooseItem,
    };
    this.getAttributeList();
  },
  methods: {
    delAttribute(item) {
      this.$confirm("是否删除该数据?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.$api
            .post("/collect/delDataProperty", {
              pro_id: item.pro_id,
            })
            .then(() => {
              this.$message.success("操作成功");
              this.getAttributeList();
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editAttribute(row) {
      this.attributeVisibleTitle = "编辑属性";
      this.attributeForm = row;
      this.attributeForm.protocol_config = !this.analyzeProperty(
        row.protocol_config
      )
        ? {}
        : {
            ...row.protocol_config,
            decimal_point: row.protocol_config.decimal_point || 1,
          };
      this.attributeVisible = true;
    },
    submitAttributeForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (this.attributeForm.pro_id) {
            this.$api
              .post("/collect/updateDataProperty", this.attributeForm)
              .then(() => {
                this.attributeVisible = false;
                this.$message.success("操作成功");
                this.getAttributeList();
              });
          } else {
            this.$api
              .post("/collect/addDataProperty", this.attributeForm)
              .then(() => {
                this.attributeVisible = false;
                this.$message.success("操作成功");
                this.getAttributeList();
              });
          }
        } else {
          return false;
        }
      });
    },
    addAttribute() {
      this.attributeVisibleTitle = "添加属性";
      this.attributeForm = {
        source_id: this.chooseItemInfo.source_id,
        protocol_config: {
          start_address: 0,
          scale_factor: 1,
          decimal_point: 1,
          report_method: 2,
        },
      };
      this.attributeVisible = true;
    },
    getDataValueList() {
      this.getData();
    },
    getAttributeList() {
      this.attributeList = [];
      this.loading = true;
      this.$api
        .get(`/collect/getDataPropertyList`, {
          params: {
            ...this.attributeParams,
            source_id: this.chooseItemInfo.source_id,
          },
        })
        .then(async (res) => {
          if (res.data.data) {
            this.attributeList = res.data.data.map((item) => ({
              ...item,
              protocol_config: item.protocol_config
                ? JSON.parse(item.protocol_config)
                : {},
            }));

            this.attributeTotal = res.data.total;
            this.getDataValueList();
          } else {
            this.attributeList = [];
          }
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getData() {
      this.$api
        .get("/collect/getDataValueList", {
          params: {
            ...this.attributeParams,
            source_id: this.chooseItemInfo.source_id,
          },
        })
        .then((res) => {
          if (res.data.data) {
            this.attributeList.forEach((item1, index) => {
              const matchedItem = res.data.data.find(
                (item) => item1.pro_id === item.pro_id
              );
              if (matchedItem) {
                // 使用数组索引确保触发响应式
                this.$set(this.attributeList, index, {
                  ...item1,
                  base_value: matchedItem.value,
                  time: matchedItem.time,
                });
              }
            });
          }
        });
    },
    analyzeProperty(value) {
      if (
        value === null ||
        value === "null" ||
        value === "" ||
        value === undefined
      )
        return false;

      if (typeof value === "object" && value !== null) {
        if (Object.keys(value).length === 0) return false;
      }
      return true;
    },
  },
};
</script>

<style lang="scss" scoped></style>
